/*Styled only for mobile at the moment
will add iPad size styling too eventually */

@media screen and (max-width: 500px) {
    body {
        font-family: Arial, Helvetica, sans-serif;
        color: black;
        background-color: whitesmoke;
    }
    
    .header {
        display: flex;
        flex-direction: row;
        border-radius: 8px;
        background-color: white;
        margin-bottom: 10px;
        box-shadow: 1px 1px 2px 0px slategray;
    }
    
    h1{
       
        font-weight: 100;
        margin-left: 10px;
    }
    
    .titleSpan {
        color: darkgreen;
        font-weight: bold;
    }
    
    
    .uberOneSelected {
        height: 50px;
        width: 100px;
        background-color: black;
        border-color: black;
        color: goldenrod;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;
        margin-inline: 2px;
        margin-top: 30px;
        margin-right: 20px;
        box-shadow: 1px 2px 2px 1px goldenrod;
    }
    
    .uberOneNotSelected {
        height: 50px;
        width: 100px;
        background-color: whitesmoke;
        border: solid black 1px;
        color: black;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;
        margin-inline: 2px;
        margin-top: 30px;
        margin-right: 20px;
    }
    
    .options {
        display: flex;
        width: 100%;
        height: 35px;
        padding-top: 15px;
        padding-bottom: 15px;
        gap: 5%;
        background-color: white;
        border-radius: 6px;
        box-shadow: 1px 1px 2px 0px slategray;
    }
    
    .input {
        border-radius: 6px;
        font-size: 18px;
        text-align: center;
        width: 100%;
        border: solid slategray 2px;
        margin-inline: 20px;
        background-color: white;
        
    }


    .users {
        display: flex;
        flex-direction: row;
        width: 103%; 
        gap: 10px; 
        font-size: 18px;
        margin-top: 3%;
        overflow-x: scroll;
        white-space: nowrap;
        
    }
    
    .user {
        height: 80px;
        min-width: 90px;
        background-color: white;
        color: black;
        border: solid black 2px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;
        margin-inline: 2px;
        font-size: 14px;
        box-shadow: 1px 1px 2px 0px slategray;
    }
    
    .activeUser {
        height: 80px;
        min-width: 90px;
        background-color: black;
        border-color: black;
        color: whitesmoke;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;
        margin-inline: 2px;
        font-size: 16px;
        box-shadow: 1px 1px 2px 0px slategray;
    }
    
    .addToOrder{
        display: flex;
        font-size: 18px;
        background-color: white;
        border-radius: 8px;
        margin-top: 3%;
        margin-bottom: 3%;
        box-shadow: 1px 1px 2px 0px slategray;
    }
    
    .itemValue {
        width: 33%;
        margin-top: 15px;
        margin: 3%;
        text-align: center;
        height: 30px;
        border-radius: 6px;
        font-size: 16px;
        border: solid slategray 2px;
        background-color: white;
    }
    
    .quantityInput {
        width: 33%;
        margin-top: 15px;
        margin: 3%;
        text-align: center;
        height: 30px;
        border-radius: 6px;
        font-size: 16px;
        border: solid slategray 2px;
        background-color: white;;
    }


    p {
        text-align: center;
    }
    
    .button {
        width: 45%;
        height: 50px;
        margin: 2%;
        border-radius: 6px;
        font-size: 15px;
        border: solid slategray 2px;
        color: black;
        background-color: white;
    }
    
    .button:active {
        background-color: black;
        color: white;
    }
  
    .button:disabled {
      color: lightgray;
    }
    
    .button:disabled:active{
        background-color: whitesmoke;
    }
    
    .addUserButton {
        width: 25%;
        float: right;
        height: 47px;
        margin: 3%;
        margin-top: 4%;
        margin-right: 20px;
        border-radius: 6px;
        font-size: 14px;
        background-color: black;
        border-color: black;
        color: whitesmoke;
        box-shadow: 1px 1px 2px 0px slategray;
    }
    
    .addUserButton:active {
        background-color: black;
        color: white;
    }
    
    .currentUser{
        padding: 5%;
        font-size: 16px;
        background-color: white;
        border-radius: 8px;
        margin-top: 3%;
        box-shadow: 1px 1px 2px 0px slategray;
    }
    
    .totals {
        width: 100%;
        font-size: 18px;
        border-radius: 6px;
        background-color: white;
    }
    
    .subtotalEntry {
        padding: 2%;
        border-bottom: solid lightgray 1px;
        width: 90%;
        margin-inline: 3%;
        background-color: whitesmoke;
    }
    
    .subtotalEntry:nth-child(odd){
        background-color: white;
    }
    
    .totalEntry{
        padding: 2%;
        border-bottom: solid lightgray 1px;
        font-size: 18px;
        width: 90%;
        margin-inline: 3%;
        background-color: white;
    }
    
    .totalEntry:nth-child(even){
        background-color: whitesmoke;
    }
    
    .totalsSpan {
        float: right;
    }
    
    
    .calculateButton {
        width: 90%;
        margin: 5%;
        height: 60px;
        border-radius: 12px;
        font-size: 16px;
        background-color: black;
        color: white;
    }
    
    .calculateButton:active {
        color: black;
        background-color: whitesmoke;
    }
    
    .grandTotal {
        font-weight: bold;
        margin-inline: 20%;
        margin-bottom: 5%;
        font-size: 20px;
        padding-top: 5%;
    }
    }